            icon="pi pi-check"
            class="p-mr-2"
            autofocus
            @click="save"
        />
        <p-button
            label="Cancel"
            icon="pi pi-times"
            class="p-button-outlined p-button-danger"
            @click="close"
        />
    </p-dialog>
</template>

<script>
import { computed, reactive, ref } from "vue";
import { emitter } from "@/event-bus";
import { useStore } from "vuex";
import { useFormatToFromData } from "@/composable/cycle-monitoring";
export default {
    setup() {
        const store = useStore();
        const selectedLine = computed(
            () => store.state.cycleMonitoring.selectedMonitorLine
        );
        const visible = ref(false);
        const type = ref("edit");
        const ovaryFollicles = reactive([
            {
                type: "Right Ovary Follicles",
                input10l: null,
                input11: null,
                input12: null,
                input13: null,
                input14: null,
                input15: null,
                input16: null,
                input17: null,
                input18: null,
                input19: null,
                input20: null,
                input21: null,
                input22: null,
                input23: null,
                input24g: null
            },
            {
                type: "Left Ovary Follicles",
                input10l: null,
                input11: null,
                input12: null,
                input13: null,
                input14: null,
                input15: null,
                input16: null,
                input17: null,
                input18: null,
                input19: null,
                input20: null,
                input21: null,
                input22: null,
                input23: null,
                input24g: null
            }
        ]);

        const form = reactive({
            cmLineId: null,
            date: null,
            cycleId: null,
            monitorId: null,
            cycleDay: null,
            injecttionDay: null,
            rightOvaryTotal: null,
            rightOvaryCyst: null,
            leftOvaryTotal: null,
            leftOvaryCyst: null,
            et: null,
            polyp: null,
            gnt: null,
            comments: null
        });

        const save = () => {
            let formData = useFormatToFromData(
                store.state.cycleMonitoring.details,
                { ...form },
                ovaryFollicles[0],
                ovaryFollicles[1]
            );

            if (type.value === "new") {
                store
                    .dispatch("cycleMonitoring/createMonitorLine", formData)
                    .then(() => {
                        emitter.emit("cycle-monitoring:reload");
                        visible.value = false;
                    });
            }
            if (type.value === "edit") {
                store
                    .dispatch("cycleMonitoring/editMonitorLine", {
                        ...formData,
                        monId: form.monitorId,
                        nlmLineId: form.cmLineId
                    })
                    .then(() => {
                        emitter.emit("cycle-monitoring:reload");
                        visible.value = false;
                    });
            }
        };

        emitter.on("cycle-monitoring-day-toolbar:edit-day", () => {
            visible.value = true;
            type.value = "edit";
        });
        emitter.on("cycle-monitoring-day-toolbar:new-day", () => {
            visible.value = true;
            type.value = "new";
        });

        const onShow = () => {
            const right = ovaryFollicles[0];
            const left = ovaryFollicles[1];

            if (type.value === "edit") {
                form.cmLineId = selectedLine.value.cm_lines_id;
                form.date = new Date(selectedLine.value.cycleDate);
                form.cycleId = store.state.cycleMonitoring.details.cycleId;
                form.monitorId = store.state.cycleMonitoring.details.monitorId;
                form.cycleDay = parseInt(selectedLine.value.cycleDay);
                form.injecttionDay = parseInt(selectedLine.value.injectionDay);
                form.rightOvaryTotal = parseInt(selectedLine.value.rTotal);
                form.rightOvaryCyst = selectedLine.value.rCyst;
                form.leftOvaryTotal = parseInt(selectedLine.value.lTotal);
                form.leftOvaryCyst = selectedLine.value.lCyst;
                form.et = Number(selectedLine.value.etUs);
                form.polyp = selectedLine.value.polyp;
                form.gnt = selectedLine.value.gnt;

                right.input10l = selectedLine.value.or10ls;
                right.input11 = selectedLine.value.or11;
                right.input12 = selectedLine.value.or12;
                right.input13 = selectedLine.value.or13;
                right.input14 = selectedLine.value.or14;
                right.input15 = selectedLine.value.or15;
                right.input16 = selectedLine.value.or16;
                right.input17 = selectedLine.value.or17;
                right.input18 = selectedLine.value.or18;
                right.input19 = selectedLine.value.or19;
                right.input20 = selectedLine.value.or20;
                right.input21 = selectedLine.value.or12;
                right.input22 = selectedLine.value.or22;
                right.input23 = selectedLine.value.or23;
                right.input24g = selectedLine.value.or24gt;

                left.input10l = selectedLine.value.ol10ls;
                left.input11 = selectedLine.value.ol11;
                left.input12 = selectedLine.value.ol12;
                left.input13 = selectedLine.value.ol13;
                left.input14 = selectedLine.value.ol14;
                left.input15 = selectedLine.value.ol15;
                left.input16 = selectedLine.value.ol16;
                left.input17 = selectedLine.value.ol17;
                left.input18 = selectedLine.value.ol18;
                left.input19 = selectedLine.value.ol19;
                left.input20 = selectedLine.value.ol20;
                left.input21 = selectedLine.value.ol12;
                left.input22 = selectedLine.value.ol22;
                left.input23 = selectedLine.value.ol23;
                left.input24g = selectedLine.value.ol24gt;
            }

            if (type.value === "new") {
                form.date = null;
                form.cycleId = store.state.artCycle.selectedCycle.cycleId;
                form.cycleDay = null;
                form.injecttionDay = null;
                form.rightOvaryTotal = null;
                form.rightOvaryCyst = null;
                form.leftOvaryTotal = null;
                form.leftOvaryCyst = null;
                form.et = null;
                form.polyp = null;
                form.gnt = null;
                right.input10l = null;
                right.input11 = null;
                right.input12 = null;
                right.input13 = null;
                right.input14 = null;
                right.input15 = null;
                right.input16 = null;
                right.input17 = null;
                right.input18 = null;
                right.input19 = null;
                right.input20 = null;
                right.input21 = null;
                right.input22 = null;
                right.input23 = null;
                right.input24g = null;
                left.input10l = null;
                left.input11 = null;
                left.input12 = null;
                left.input13 = null;
                left.input14 = null;
                left.input15 = null;
                left.input16 = null;
                left.input17 = null;
                left.input18 = null;
                left.input19 = null;
                left.input20 = null;
                left.input21 = null;
                left.input22 = null;
                left.input23 = null;
                left.input24g = null;
            }
        };

        return {
            visible,
            ovaryFollicles,
            type,
            form,
            save,
            onShow,
            close: () => (visible.value = false)
        };
    }
};
</script>

