<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <label for="next-plans" class="p-col-fixed p-mb-1">
                            Next Plans
                        </label>
                        <!-- nextplan -->
                        <div class="p-col">
                            <p-input-text v-model="form.nextPlan" id="next-plans" type="text" />
                        </div>
                    </div>
                </div>
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <!-- consents -->
                        <label for="consent" class="p-col-fixed p-mb-1">
                            Consent
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.consents" id="consent" type="text" />
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, reactive, watch } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        const form = reactive({
            nextPlan: null,
            consents: null
        });

        watch(monitorDetails, newValue => {
            form.nextPlan = newValue.nextPlan;
            form.consents = newValue.consents;
        });

        return { form };
    }
};
</script>
