import { format } from "date-fns";

export function useFormatToFromData(details, line, left, right) {
    return {
        cycleId: details.cycleId, //required
        monId: details.monitorId, //required
        curPatientId: details.patientId, //required
        isSigned: true, //required
        nmlDate: format(line.date, "yyyy-MM-dd"),
        nmlCDay: line.cycleDay,
        nmlIDay: line.injecttionDay,
        nmlRTot: line.rightOvaryTotal,
        nmlLTot: line.leftOvaryTotal,
        nmlR10ls: right.input10l,
        nmlR11: right.input11,
        nmlR12: right.input11,
        nmlR13: right.input11,
        nmlR14: right.input11,
        nmlR15: right.input11,
        nmlR16: right.input11,
        nmlR17: right.input11,
        nmlR18: right.input11,
        nmlR19: right.input11,
        nmlR20: right.input11,
        nmlR21: right.input11,
        nmlR22: right.input11,
        nmlR23: right.input11,
        nmlR24gt: right.input11,
        nmlL10ls: left.input11,
        nmlL11: left.input11,
        nmlL12: left.input11,
        nmlL13: left.input11,
        nmlL14: left.input11,
        nmlL15: left.input11,
        nmlL16: left.input11,
        nmlL17: left.input11,
        nmlL18: left.input11,
        nmlL19: left.input11,
        nmlL20: left.input11,
        nmlL21: left.input11,
        nmlL22: left.input11,
        nmlL23: left.input11,
        nmlL24gt: left.input11,
        nmlET: line.et,
        nmlGnT: line.gnt,
        nmlRCyst: line.rightOvaryCyst,
        nmlLCyst: line.leftOvaryTotal,
        nmlPolyp: line.polyp,
        nmlComment: line.comments
    };
}
